ve();\n        node.remove();\n\n        Array.prototype.push.apply(store.links, links);\n\n        link = d3.select(\"#chase\").append(\"g\")\n            .attr(\"class\", \"links\")\n            .selectAll(\"line\")\n            .data(store.links)\n            .enter().append(\"line\")\n            .attr(\"stroke-width\", 3);\n\n        Array.prototype.push.apply(store.nodes, nodes);\n        store.nodes = _.uniq(store.nodes, 'id');\n\n        node = d3.select(\"#chase\").append(\"g\")\n            .attr(\"class\", \"nodes\")\n            .selectAll(\"circle\")\n            .data(store.nodes)\n            .enter().append(\"circle\")\n            .attr(\"r\", function(d) { return d.size; })\n            .attr(\"fill\", function(d) { return colors(d.group); })\n            .on(\"click\", function(d) {alert(d.id)})\n            .call(d3.drag()\n            .on(\"start\", dragstarted)\n            .on(\"drag\", dragged)\n            .on(\"end\", dragended)); \n\n        simulation\n            .nodes(store.nodes)\n            .on(\"tick\", ticked)\n\n        simulation.force(\"link\")\n            .links(store.links);\n\n        function ticked() {\n            link\n              .attr(\"x1\", function(d) { return d.source.x; })\n              .attr(\"y1\", function(d) { return d.source.y; })\n              .attr(\"x2\", function(d) { return d.target.x; })\n              .attr(\"y2\", function(d) { return d.target.y; });\n\n            node\n              .attr(\"cx\", function(d) { return d.x; })\n              .attr(\"cy\", function(d) { return d.y; });\n\n            };\n    }\n\n</script>\n"],"sourceRoot":""}]);

